	module.exports = {"data":{"markdownRemark":{"html":"<h2>Footprinting and Reconnaissance</h2>\n<p>Footprinting or Reconnaissance is the process of gathering information about the target before an attack is carried out by a hacker.In a footprinting phase, the hacker will try to collect the maximum amount of information he can about the target which can be useful in an attack such as:</p>\n<ol>\n<li>Identifying the operating system</li>\n<li>Scanning ports to detect open ones for exploitation</li>\n<li>WHOIS queries used to know about registrar information of a particular service.</li>\n<li>Crawling the web to know more about the target.</li>\n<li>Collecting phone numbers,email addresses,employee information &#x26; other insider information.</li>\n</ol>\n<p>In short,footprinting is the first most and an essential phase of penetration testing without which it can be very difficult to perform a hack.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->","fields":{"slug":"/security/footprinting-and-reconnaissance/"},"frontmatter":{"title":"Footprinting and Reconnaissance"}}},"pathContext":{"slug":"/security/footprinting-and-reconnaissance/"}}

